@import url('https://fonts.googleapis.com/css2?family=Signika+Negative:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Signika Negative';
  }

section {
    display: flex;
    flex-wrap: wrap;
    min-height: 100vh;
}

#inicio {
    display: flex;
    min-height: 94vh;
    justify-content: space-evenly;
}

.imagem::before {
    content: "";
    position: absolute;
    top:10%;
    background-color: #008F8C;
    width: 100%;
    height: 80%;
    z-index: 0;
}

.fotoInicial {
    position: absolute;
    width: 95%;
    bottom: 0;
}

nav {
    display: flex;
    justify-content: flex-end;
    padding: 10px;
    padding-right: 50px;
    font-size: 1.2em;
    font-weight: 800;
    box-shadow: 0px 1px 4px rgb(0,0,0, 0.25);
}

nav a {
    padding-right: 50px;
    text-decoration: none;
    color: #015958;
}

h1 {
    color: #015958;
    text-align: center;
    margin: 15px;
}

.apresentacao {
    display: flex;
    flex-direction: column;
    width: 35%;
    margin-top: 50px;
}

span {
    padding-left: 30px;
    font-size: 1.1em;
}

.imagem {
    display: flex;
    justify-content: center;
    width: 450px;
    position: relative;
}

#sobreMim {
    justify-content: space-evenly;
}

.textoSobreMim, .habilidades {
    height: 400px;
    background-color: white;
    padding: 5px;
    box-shadow: 1rem 20px 4px #008F8C;
    margin-top: 30px;
    margin-bottom: 30px;
}

.textoSobreMim {
    width: 450px;
    text-align: justify;
    font-size: 1.1em;
    padding-left: 20px;
    padding-right: 20px;
    min-height: 400px;
}


.habilidades {
    display: flex;
    flex-direction: column;
    min-width: 250px;
}

.habilidades li {
    font-size: 1.1em;
    margin-left: 20px;
    padding: 5px;
    list-style: none;
}

.sobreMim {
    background-color: rgb(243, 241, 241);
    align-items: center
}

h3 {
    background-color: #008F8C;
}

#projetos {
    flex-direction: column;
}

.projetos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 100%;
}

.imgProjeto {
    width: 300px;
}

.projeto {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 250px;
    background-color: #015958;
    margin: 10px;
}

.projeto a {
    text-decoration: none;
    color: white;
}

.projeto p,.projeto h4{
    color: white;
}

h4 {
    margin: 2px;
}

footer {
    height: auto;
    width: 100%;
    box-shadow: 1rem -1px 4px rgb(0,0,0, 0.25);
    margin: 0;
    padding: 5px;
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: flex-end;
}

footer a {
    padding-right: 10px;
}

@media screen and (max-width: 730px){
   #inicio {
    height: auto;
   }
   
    .imagem, .apresentacao {
    width: 90%;
   }

   .fotoInicial{
    width: 80%;
    position: inherit;
   }

   .imagem::before{
    top:5px;
    width: 80%;
   }

   .apresentacao{
    height: 150px;
   }

   nav{
    padding-right: 0;
   }

   nav a {
    padding-right: 20px;
   }

   .textoSobreMim{
    width: 320px;

   }

   body{
    font-size: 0.8em;
   }

}